@import "sass/index";

$imgSize: 16px;

.wrapper {
  width: 100%;
}

.dialogActions {
  padding: 0;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: spacing(1.25);
  padding-top: 12px;
  padding-bottom: 36px;

  @media (max-width: $mobileScreen) {
    grid-template-columns: repeat(2, 1fr);
  }
}

.button {
  height: 41px;
  min-width: 100px;

  // & svg {
  //   opacity: 1;
  // }
  // & svg g {
  //   stroke: $m_dark;
  //   path {
  //     fill: $m_dark;
  //   }
  // }
  & svg {
    opacity: 1;
  }
  & svg g {
    stroke: $m_dark;
  }
  .exchangeIcon {
    & svg path {
      fill: $m_dark;
    }
  }
  .vaultIcon {
    & svg g {
      stroke: transparent;
      path {
        fill: black;
      }
    }
  }
}
.transparentButton {
  background: none;
  box-shadow: none;
  & svg {
    opacity: 0.5;
  }
  & svg g {
    stroke: white;
  }
  .exchangeIcon {
    & svg path {
      fill: white;
    }
  }
  .vaultIcon {
    & svg g {
      stroke: transparent;
      path {
        fill: white;
      }
    }
  }
}

.select {
  height: 60px;
}
.selectInput {
}

.label {
  font-size: 12px;
  letter-spacing: 0.86px;
  margin-left: 8px;
}
.depositIcon {
  display: flex;
  justify-content: center;

  & svg {
    height: $imgSize;
    width: $imgSize;
  }
}
.withdrawIcon {
  display: flex;
  justify-content: center;

  & svg {
    height: $imgSize;
    width: $imgSize;
  }
}
.recordsIcon {
  display: flex;
  justify-content: center;

  & svg {
    height: 13px;
    width: 18px;
  }
}
.billsIcon {
  display: flex;
  justify-content: center;

  & svg {
    height: 20px;
    width: $imgSize;
  }
}
.exchangeIcon {
  display: flex;
  justify-content: center;

  & svg {
    height: 22px;
    width: $imgSize;
  }
}
.vaultIcon {
  display: flex;
  justify-content: center;

  & svg {
    height: 22px;
    width: 20px;
  }
}
.tabs {
  max-height: MIN(calc(100vh - 319px - #{spacing(8)}), 574px);
  @media (max-width: $mobileScreen) {
    max-height: initial;
  }
}

.reminderText {
  font-size: 13px;
  color: rgba(white, 0.5);
  font-family: Helvetica;
}
.boldText {
  font-weight: bold;
}
.modalRoot {
  justify-content: flex-start;
  background-position: center -4px;
}
